:root {
  --chunjo: #dea02f;
  --jinno: #181e89;
  --shinsoo: #9d0000;
  --btn-chunjo: #dea02f;
  --btn-jinno: #1f27c2;
  --btn-shinsoo: #d32626;
}

.Map {
  display: inline-grid;
  grid-column: 2;
  grid-row: 1 / 3;
  width: 800px;
  height: 800px;
  background-color: rgba(0, 0, 0, 0);
}

.Map div#wrapper {
  min-width: 800px;
  height: 100%;
}

.Map h3 {
  margin: 0;
  padding:  0 8px 0 8px;
  width: calc(100% - 16px);
  font-size: 20px;
  line-height: 36px;
}

.Map div#container-buttons {
  width: 800px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.Map div#container-images {
  position: relative;
  margin-left: 8px;
}

.Map img#map {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 50;
  outline: 2px solid #999999;
  border-radius: 16px;
}

.Map img#spawn {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 100;
}

.Map img.img-map {
  height: 616px;
}

.Map button.btn-area {
  min-width: 110px;
  height: 36px;
  margin: 6px 0 6px 12px;
  padding: 0 8px 0px 8px;
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid #aaaaaa;
  border-radius: 18px 18px 18px 18px;
  box-shadow: 0px 0px 3px 1px #aaaaaa;
}

.Map button.btn-area:hover {
  background-color: rgba(255, 255, 255, 0.3);
  transition: 0.25s;
}

.Map button.btn-area:active {
  background-color: rgba(255, 255, 255, 0.05);
  transition: 0s;
}

.Map button.btn-area.active {
  background-color: rgba(255, 255, 255, 0.25);
}

.Map button.btn-area.active:hover {
  background-color: rgba(255, 255, 255, 0.3);
  transition: 0.25s;
}

.Map button.btn-joan, .Map button.btn-bokjung {
  border: 2px solid var(--btn-chunjo);
  box-shadow: 0px 0px 3px 1px var(--btn-chunjo);
}

.Map button.btn-pyungmoo, .Map button.btn-bakra {
  border: 2px solid var(--btn-jinno);
  box-shadow: 0px 0px 3px 1px var(--btn-jinno);
}

.Map button.btn-yongan, .Map button.btn-yayang {
  border: 2px solid var(--btn-shinsoo);
  box-shadow: 0px 0px 3px 1px var(--btn-shinsoo);
}

.Map img.img-map {
  min-width: 320px;
  min-height: 320px;
  max-height: 616px;
}

.Map img#map {
  background: url("../../images/image-unavailable.png") no-repeat scroll 0 0;
  background-size: 320px;
}