.MissionLevelSelector {
  display: inline-grid;
  grid-column: 1;
  grid-row: 1 / 3;
  width: 600px;
  height: 150px;
  background-color: rgba(0, 0, 0, 0);
}

.MissionLevelSelector div#wrapper {
  width: 600px;
  height: 100%;
}

.MissionLevelSelector h3 {
  margin: 0;
  padding:  0 8px 0 8px;
  width: calc(100% - 16px);
  font-size: 20px;
  line-height: 36px;
}

.MissionLevelSelector div#button-wrapper {
  display: flex;
  width: 100%;
  height: calc(100% - 36px);
  justify-content: flex-start;
  align-content: center;
  flex-wrap: wrap;
  
}

.MissionLevelSelector button.btn-mission-lvl {
  width: 110px;
  height: 36px;
  margin: 0 0 12px 12px;
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid #aaaaaa;
  border-radius: 18px 18px 18px 18px;
  box-shadow: 1px 0px 3px 1px rgba(211, 255, 242, 0.75);
  white-space: nowrap;
  overflow: hidden;
}

.MissionLevelSelector button.btn-mission-lvl:hover {
  background-color: rgba(255, 255, 255, 0.3);
  transition: 0.25s;
}

.MissionLevelSelector button.btn-mission-lvl:active {
  background-color: rgba(255, 255, 255, 0.05);
  transition: 0s;
}

.MissionLevelSelector button.active {
  background-color: rgba(255, 255, 255, 0.2);
  border: 2px solid #dddddd;
}

.MissionLevelSelector button.btn-mission-lvl:disabled {
  filter: contrast(50%);
}

.MissionLevelSelector button.btn-mission-lvl:hover:disabled {
  background-color: rgba(255, 255, 255, 0.1);
  transition: 0.25s;
}

.MissionLevelSelector button.btn-mission-lvl:active:disabled {
  background-color: rgba(255, 255, 255, 0.1);
  transition: 0s;
}

.MissionLevelSelector button.active:disabled {
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid #dddddd;
}

.MissionLevelSelector button.range5 {
  cursor: not-allowed;
}

.MissionLevelSelector button.range6 {
  cursor: not-allowed;
}

.MissionLevelSelector button.range7 {
  cursor: not-allowed;
}